import React, { Component } from "react";
import $ from 'jquery'
import { Dropdown, Tooltip } from 'react-bootstrap';
import ProfileImage from '../../../../CommonComponents/ProfileImage/ProfileImage';
import { COPY_TO_CLIPBOARD } from "../../../../../../Services/Utils/utils";
import { tinyRequest } from '../../../../../../Services/Actions/tinyUrlAction'

class GetRightsCopyUrl extends Component {
    constructor() {
        super();
        this.state = { codeCopied: false };
    }

    codeCopy = (id) => {
        const { modalAssetData } = this.props
        var self = this;
        this.setState({codeCopied: true})
        setTimeout(() => {
            this.setState({codeCopied: false})
        }, 1500);
        COPY_TO_CLIPBOARD(id);
        tinyRequest(modalAssetData.link).then((response) => {
            const { data } = response.data
            self.onRedirect(modalAssetData, data.tiny_url)
        }).catch((ex) => {
            self.onRedirect(modalAssetData, modalAssetData.link)
        })

    }

    onRedirect = (modalAssetData, link, type) => {
        const { rightsVia } = this.props
        if (rightsVia == "viaComments") {
            navigator.clipboard.writeText($('#custom_message').val() + ' Post : ' + link + ' Form Link : ' + $('#ugc_rights_url_').val());
            setTimeout(() => window.open(link, '_blank'), 400)
        }
        else if(rightsVia == "viaDM") {
            navigator.clipboard.writeText($('#custom_message').val() + ' Post : ' + link + ' Form Link : ' + $('#ugc_rights_url_').val());
            if (modalAssetData.networkId == 18 || modalAssetData.networkId == 2) setTimeout(() => window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank'), 400)
            else setTimeout(() => window.open(link, '_blank'), 400)
        }
        else if(rightsVia == "linkBio"){
            navigator.clipboard.writeText($('#custom_message').val())
            type == "bio_comment" ? window.open(link, '_blank') : 
            modalAssetData.networkId == 18 ? window.open(`https://ig.me/m/${modalAssetData.username}`, '_blank') : window.open(`https://www.tiktok.com/messages?u=${modalAssetData.userId}`,'_blank')
        }
    }

    render() {

        const { codeCopied } = this.state
        const { requestedRespone, comment, modalAssetData, rightsVia, accountList, selectedAccount, onChangeSelectedAccount, hashValue, hashChange, sendNow, isRequesting, onChangeComment, hashError, bioLink, accountError } = this.props
        return rightsVia != "viaComments" && requestedRespone && Object.keys(requestedRespone).length > 0 ? <><div className="p-lg-6 p-3">
            <h5 className="mb-2 pt-1 text-center">Your UGC Request Sharing URL</h5>
            <p className="text-muted text-center">Copy and paste link in the comments or original post</p>
            <div className="mb-6">
                <textarea type="text" id="custom_message" className="form-control form-control-lg textarea-min-row-3 bg-light" rows={4} placeholder="Add Comment" defaultValue={comment} readOnly />
            </div>
            <div className="mb-6">
                {rightsVia != "linkBio" ? <>
                    <div className="input-group mb-4">
                        <div className="input-group-text">
                            <i className="ri-link icon-4"></i>
                        </div>
                        <input type="text" id="ugc_rights_url_" className="form-control form-control-lg" defaultValue={requestedRespone.bitlylink} readOnly />
                    </div>
                    <button type="button" className="btn btn-primary btn-lg w-100" onClick={() => { this.codeCopy('ugc_rights_url_') }}>
                        {codeCopied ? 'Copied' : 'Copy URL'}
                    </button>
                </> : 
                <button type="button" className="btn btn-primary btn-lg w-100" onClick={() => this.onRedirect(modalAssetData, modalAssetData.link,"") }>Copy and Send to DM
                </button>}
            </div>    
            <p className="mb text-muted"><span className="fw-semibold">Note: </span>You're all set! Your "UGC Request" regarding this post is ready for action.</p>
            <div className="mb-6">
                <h6>Steps to proceed</h6>
                {rightsVia == "linkBio" ?
                    <ul className="list-unstyled list-points fs-7">
                        <li className="mb-3">Copy the below URL and place it in your bio!</li>
                        <li className="mb-3">Simply copy the message you'd like to use and choose how you would like to ask for a request; you will have 2 options: via DM or via Comments. </li>
                        <li className="mb-3">Paste the message you copied or add a polite request to the user to grant approval by visiting the link in your bio.</li>
                        <li className="mb-3">Once you've sent your request, a handy tracking feature will activate. You can conveniently check the approval status right from the dedicated icon in the app.</li>
                        <li className="mb-3">If the creator grants permission, you'll receive an official approval notification sent directly to your email.</li>
                    </ul> 
                :   <ul className="list-unstyled list-points fs-7">
                        <li className="mb-3">Review the request message before sending it to the creator.</li>
                        <li className="mb-3">Click on the URL button and you will be redirected to the creatorâ€™s Direct Message.</li>
                        <li className="mb-3">Copy and paste the URL into the inbox, then send it to the creator.</li>
                        <li   li className="mb-3">Your UGC request will remain valid for 30 days. After this period, the creator will no longer be able to approve your request.</li>
                    </ul>}
            </div>

            { rightsVia == "linkBio" ?  <div>
                <p className="mb-1 text-muted"><span className="fw-semibold">Link in Bio url</span></p>
                <div className="input-group mb-4">
                    <div className="input-group-text">
                        <i className="ri-link icon-4"></i>
                    </div>
                    <input type="text" id="ugc_rights_url_" className="form-control form-control-lg" defaultValue={bioLink} readOnly />
                </div>
                <div className="separator separator-content my-4 w-100">
                    <span className="text-gray-500 fw-semibold fs-8 block-center text-nowrap" style={{ width: 120 }}>Alternate Request Option</span>
                </div>
                <button type="button" className="btn btn-primary btn-lg w-100" onClick={() => this.onRedirect(modalAssetData, modalAssetData.link,"bio_comment") }>Copy and Comment on Post
                </button>
            </div> :  null }
        </div> </> : rightsVia == "viaComments" ? <>
            <div className="p-lg-6 p-3">
                <h5 className="mb-2 pt-1 text-center">Your UGC Request </h5>
                <h3 className="mb-2 fs-7 mt-3">
                    <span className="fw-medium">Social Account <strong className="text-danger">*</strong></span>
                </h3>

                <Dropdown style={{ minWidth: 220 }} className={`${accountError ? "is-invalid" : ""}`}>
                    <Dropdown.Toggle variant="secondary" size={'sm'} className={`d-flex align-items-center w-100 justify-content-between bg-white ${accountError ? "is-invalid" : ""}`}>
                        {selectedAccount && Object.keys(selectedAccount).length > 0 ? <div className="d-flex align-items-center flex-grow-1">
                            <ProfileImage className={'symbol symbol-26 symbol-circle bg-light flex-shrink-0 me-2'}
                                imageSize={26}
                                symbolClass={'symbol-label text-light-alt fw-bold text-uppercase fs-8'}
                                name={selectedAccount.userName} profileUrl={selectedAccount.ConnectedAccounts.profilePicture} networkId={selectedAccount.ConnectedAccounts.networkId} />
                            <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                                <p className="text-gray-800 fw-medium mb-0 text-ellipsis fs-9">{selectedAccount.userName}</p>
                            </div>
                        </div> : <div className="d-flex align-items-center flex-grow-1">
                            <i className="ri-account-circle-fill icon-1 text-muted me-2"></i>
                            <div className="overflow-hidden w-100">
                                <p className="fw-medium mb-0 text-ellipsis fs-8 text-start">Add {accountList && accountList.length > 0 ? `Another ` : ``} Account</p>
                            </div>
                        </div>}

                    </Dropdown.Toggle>

                    <Dropdown.Menu className="w-100">
                        <div className="fs-7 px-3 py-2 fw-semibold text-gray-500">Select Account:</div>
                        {accountList.map(account => {
                            const { accountId, profilePicture, accountName, networkId } = account.ConnectedAccounts;
                            return <Dropdown.Item key={account.userName} onClick={onChangeSelectedAccount(account)}>
                                <div className="d-flex align-items-center">
                                    <ProfileImage className={'symbol symbol-26 symbol-circle bg-light flex-shrink-0 me-2'}
                                        imageSize={26}
                                        symbolClass={'symbol-label text-light-alt fw-bold text-uppercase fs-8'}
                                        name={account.userName} profileUrl={profilePicture} networkId={networkId} />
                                    <div className="overflow-hidden" style={{ maxWidth: 130 }}>
                                        <p className="text-gray-800 fw-medium mb-0 text-ellipsis fs-9">{account.userName} </p>
                                    </div>
                                </div>
                            </Dropdown.Item>
                        })}
                        <Dropdown.Item className="align-items-center justify-content-between" onClick={onChangeSelectedAccount(null)}>
                            <div className="d-flex align-items-center justify-content-start">
                                <i className="ri-account-circle-fill icon-1 text-muted me-2"></i>
                                <div className="fw-medium fs-8 text-dark">Add {accountList && accountList.length > 0 ? `Another ` : ``}Account</div>
                            </div>
                            <i className="ri-add-circle-line icon-3 text-muted ms-2"></i>
                        </Dropdown.Item>
                    </Dropdown.Menu>
                </Dropdown>

                {accountError ? <div className="invalid-feedback">{`Social Account is required.`}</div> : null}

                <h3 className="mb-2 fs-7 mt-3">
                    <span className="fw-medium">Enter Hashtag for UGC Rights <strong className="text-danger">*</strong></span>
                </h3>
                <div className="mb-4">
                    <input type="text" id="ugc_rights_hash_value" className={`form-control form-control-lg ${hashError?"is-invalid":''}`} value={hashValue} onChange={hashChange} />
                    {hashError && <div className="invalid-feedback d-block">Please add "#" in the message below.</div>}
                </div>

                <div className="mb-6">
                    <textarea type="text" id="custom_message" className="form-control form-control-lg textarea-min-row-3 bg-light" rows={4} placeholder="Add Comment" value={comment} onChange={onChangeComment} />
                </div>
                <div className="mb-6">
                    <div className="input-group mb-4 d-none">
                        <div className="input-group-text">
                            <i className="ri-link icon-4"></i>
                        </div>
                        {/* defaultValue={requestedRespone.bitlylink} */}
                        <input type="text" id="ugc_rights_url_" className="form-control form-control-lg" defaultValue={modalAssetData.link} readOnly />
                    </div>
                    <button type="button" className={`btn btn-primary btn-lg w-100 ${isRequesting ? `spinner disabled` : ``}`} onClick={sendNow}>
                        Send Now
                    </button>
                </div>
                <p className="mb text-muted"><span className="fw-semibold">Note: </span>You're all set! Your "UGC Request" regarding this post is ready for action.</p>
                <div className="mb-6">
                    <h6>Steps to proceed</h6>
                    <ul className="list-unstyled list-points fs-7">
                        <li className="mb-3">Review the request message before sending it to the creator.</li>
                        <li className="mb-3">Click on the Post  button and you will be directed to login to the platform[if not logged in already]. We recommend signing in through an official account to ensure authenticity.</li>
                        <li className="mb-3">You will then be redirected to the comment section under the post you desire to acquire rights for.</li>
                        <li className="mb-3">Copy and paste the message into the comment section and send.
                        </li>
                        <li className="mb-3">Your UGC request will remain valid for 30 days. After this period, the creator will no longer be able to approve your request.</li>
                    </ul>
                </div>
            </div>

        </> : null
    }
}

export default GetRightsCopyUrl;